#members {
    h2 {
        margin: 0;
        span {
            float:right;
            font-size:11px;
        }
    }
    .classList {
        display: flex;
        flex-wrap: wrap;
        &>div {
            width: calc(100% / 6);
            padding: 10px 20px 15px;
            border: 1px solid rgba(0, 204, 255, 0.2);
            border-radius: 10px;
            min-width: 160px;
            &.charclass-1 {
                color: #c69b6d;
            }
            &.charclass-2 {
                color: #f48cba;
            }
            &.charclass-3 {
                color: #aad372;
            }
            &.charclass-4 {
                color: #fff468;
            }
            &.charclass-5 {
                color: #f0ebe0;
            }
            &.charclass-6 {
                color: #c41e3b;
            }
            &.charclass-7 {
                color: #2359ff;
            }
            &.charclass-8 {
                color: #68ccef;
            }
            &.charclass-9 {
                color: #9382c9;
            }
            &.charclass-10 {
                color: #00ffba;
            }
            &.charclass-11 {
                color: #ff7c0a;
            }
            &.charclass-12 {
                color: #a330c9;
            }
            p {
                margin: -0 0 5px;
                // text-align: center;
                font-weight: bold;
                border-bottom: 1px solid;
                text-transform: uppercase;
                font-size: 13px;
                font-family: "Ropa Sans", sans-serif;
                
                span {
                    float:right;
                }
            }
            ul {
                list-style: none inside none;
                margin: 0;
            }
        }
    }
}


